import React, { Component } from 'react';
import './style.css'

class Faq extends Component {
	constructor(props){
		super(props)
            this.state={
                  minHeight:window.innerHeight-141,
            }
	}
      componentDidMount(){
            let _this = this;
            window.onresize = function(){
                  _this.setState({minHeight:window.innerHeight-141})
            }
      }
	render() {
		return (
			<div className="Faq-container" style = {{minHeight:this.state.minHeight}}>
			<div className="faq-banner">
			<div className="center">
			<h4>FAQs</h4>
			<p className="faqEnd">更多问题，欢迎咨询</p>
			<a href="https://jinshuju.net/f/n9oW9W">
                  https://jinshuju.net/f/n9oW9W
            </a>
            </div>

            </div>
            <div className="container">
            <div className="faq-wrap">
            <h4>1、Ulord概述</h4>
            <p>Ulord 是一条点对点的价值传递公链，通过搭建区块链底层架构和数字资源分发协议，支持第三方开发商在其开源协议之上构建自己的应用程序，与众多行业合作伙伴一起形成区块链技术与应用的完整生态。基于Ulord 创建的各种规则和协议，嫁接包括文字、图片、音乐、视频、软件等在内的各类数字资源应用场景，为信息创造者与消费者提供直接的对接平台。</p>
            </div>
            <div className="faq-wrap">
            <h4>2、收益是如何计算的？</h4>
            <p>目前主链出块时间2.5分钟，每天576个区块，全年大约205056区块，每个块大约112个UT，每4年减半。</p>
            </div>
            <div className="faq-wrap">
            <h4>3、为什么选择4小时发放一次收益？</h4>
            <p>在区块链底层实现钟，每个新的区块生成都需要有100个块的成熟期，而每个块大约2.5分钟，推出每个区块大约4个小时才能成熟。u1pool选择4小时为一轮支付周期，每一轮能把上一轮新生成的区块全部支付完成，举个例子：u1pool支付时间节点是0点，4点，8点，12点，16点，20点，其中8点是支付0~4点时间段，12点是支付4~8点时间段。</p>
            </div>
            <div className="faq-wrap">
            <h4>4、矿池节点如何填写？</h4>
            <p>矿池名称：u1pool.com  端口号：18888</p>
            </div>
            <div className="faq-wrap">
            <h4>5、Ulord生产工具的下载地址？</h4>
            <p>
            <a href="http://ulord.one/download.html">http://ulord.one/download.html</a>
            <span> ，这是官网下载地址</span>
            </p>
            </div>
            <div className="faq-wrap">
            <h4>6、哪些系统版本具有相应的生产工具？</h4>
            <p>Windows（win7和win10）64位系统、Linux64位系统（ubuntu16.4, centos7.3-1611）。</p>
            </div>
            <div className="faq-wrap">
            <h4>7、如何连接到矿池挖矿？</h4>
            <p>Windows系统下，进入工具所在文件夹，修改config.json配置文件并保存，双击start.sh文件即可开始挖矿；Linux系统下，进入工具所在文件夹后，使用命令./minerd  -c  config.json开始挖矿（config.json是配置文件，可进行修改）。</p>
            </div>
            <div className="faq-wrap">
            <h4>8、如何修改config.json文件，需要修改哪里的内容？</h4>
            <p>正常情况下，仅需将文件中的地址改为自己钱包的地址，线程数改为自己想要调用的线程数。</p>
            </div>
            <div className="faq-wrap">
            <h4>9、windows挖矿机(生产工具)缺少设置CPU核数，和进程数量。</h4>
            <p>可自行在config.json文件中加入"threads" : "x",(x表示挖矿调用的核数)。</p>
            </div>
            <div className="faq-wrap">
            <h4>10、全节点钱包连续几天挖矿一直没挖到，为什么？</h4>
            <p>全节点钱包挖矿是solo挖矿，因算力小的原因，挖到矿的概率较小。想要获得稳定的收益，建议下载挖矿软件加入矿池挖矿。</p>
            </div>
            <div className="faq-wrap">
            <h4>11、为什么cpuminer挖矿工具无法运行？</h4>
            <p>该问题由多方面的原因造成，可能是处理器不兼容，也可能是系统环境不兼容。新的挖矿工具正在调试中，预计一周之内分享给大家。</p>
            </div>
            </div>
            </div>
                    )
}
}

export default Faq


