import React, { Component } from 'react';
import './style.css'
import s1 from '../../imgs/s1.jpg'
import s2 from '../../imgs/s2.jpg'
import s3 from '../../imgs/s3.jpg'
import s4 from '../../imgs/s4.jpg'
import s5 from '../../imgs/s5.jpg'
import s6 from '../../imgs/s6.jpg'
import s7 from '../../imgs/s7.jpg'
import s8 from '../../imgs/s8.jpg'
import s9 from '../../imgs/s9.jpg'
import s10 from '../../imgs/s10.jpg'
import s11 from '../../imgs/s11.jpg'
import s12 from '../../imgs/s12.jpg'
import s13 from '../../imgs/s12.jpg'
import s14 from '../../imgs/s14.jpg'


class Trainning extends Component {
	render() {
		return (
			<div className="tranning-container">
				<div className="container">
					<h4>Ulord挖矿客户端新手指南</h4>
					<p className="mb0">Ulord官网</p>
					<a href="http://ulord.one">http://ulord.one</a>
					<p className="mb0">Ulord区块浏览器</p>
					<a href="http://explorer.ulord.one/">http://explorer.ulord.one</a>
					<p className="mb0">Ulord矿池地址</p>
					<a href="http://www.u1pool.com">http://www.u1pool.com</a>
					<p>
						首先获取Ulord币有很多种方式，挖矿只是其中的一种方式，具体分配方案大家可以参考官网白皮书，下面我将详细的给大家介绍Widows64位系统下pow机制获取Ulord币的流程，也就是大家常说的挖矿。
    		</p>
					<h4>
						安装
    		</h4>
					<p>
						首先进入官网软件下载页面(http://ulord.one/download.html)，下载矿池挖矿程序，目前只支持Windwos和Linux版本（64位）
    		</p>
					<img src={s1} alt="" />
					<p>点击Win64，然后点击本地下载，下载完成以后点击安装</p>
					<img src={s2} alt="" />
					<p>点击【下一步】按钮</p>
					<img src={s3} alt="" />
					<p>选择程序安装路径，这个路径可以随意选择，建议路径不要有中文字符，下面默认单选只有我即可，完成以后点击【下一步】即可</p>
					<img src={s4} alt="" />
					<p>点击【下一步】即可</p>
					<img src={s5} alt="" />
					<p>安装完成以后点击【关闭】</p>
					<img src={s6} alt="" />
					<h4>挖矿</h4>
					<p>在桌面找到Ulord挖矿客户端并双击打开。</p>
					<img src={s7} alt="" />
					<p>我们在桌面的右下角可以看到此界面，如果没有出现这个界面双击客户端圆形图标就会出现。</p>
					<img src={s8} alt="" />
					<p>当我们设置好矿矿池地址和钱包地址池以及线程数之后就可以点击【开始挖矿】。如果显示了算力就表示您已经在挖矿了，箭头所示为当前机器的算力。</p>
					<img src={s9} alt="" />
					<p>开始挖矿以后等待5-10分钟，点击查看收益即可跳转至矿池中对应矿工的界面。</p>
					<img src={s10} alt="" />
					<p>获取钱包地址的方式：<br />
						进入官网下载钱包并安装</p>
					<img src={s11} alt="" />
					<p>轻钱包获取钱包地址，点击地址列表，然后选中地址右击复制地址即可。</p>
					<img src={s12} alt="" />
					<p>全节点钱包获取钱包地址，点击文件，然后点击收款地址。</p>
					<img src={s13} alt="" />
					<p>然后右击地址列表中某个地址，点击复制地址。</p>
					<img src={s14} alt="" />
				</div>
			</div>
		)
	}
}

export default Trainning


